.wrapper{
   
    margin-left: auto;
    margin-bottom: 16px;
    margin-top: 16px;
    text-align: center;
  font-weight: bold;
}
.userLog {
  text-align: center;
  font-weight: bold;
}
.btnUserLog {
  margin-left: 10px;
  
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  width: 90px;
  cursor: pointer;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
.btnUserLog:hover {
  background-color: rgb(7, 87, 167);
  color: #fff;
}